const tailwindColors = require("tailwindcss/colors");

export const colors = {
  primary: "#eab308",
  secondary: "#fde047",
  disabled: "#d4d4d8",
};

export const getBgColors = () => {
  console.log(tailwindColors);
  const res = Object.entries(tailwindColors).map((color) => {
    console.log(color);
    const res = color[1]?.map((nb) => {
      return `bg-${color[0]}-${nb}`;
    });
    return res;
  });
};
